<template>
    <div v-show="statedata.State||statedata.State == 0">
        <div class="account_top" v-if="statedata.State == 1">
            <section>
                <span></span>
                <span>{{totaldata.currencyAcount.toFixed(8)}} {{$store.state.name.currencyname}}</span>
                <span class="aoumnt colorgray">{{totaldata.cny.toFixed(2)}} CNY</span>
            </section>
            <section>
                <a @click="pageTo('merchant')"></a>
                <a @click="pageTo('record')"></a>
                <a @click="$store.state.status.addAddressModal = true"></a>
            </section>
        </div>
        <div class="account_top" v-else>
            <span></span>
        </div>
        <div class="account_list" v-if="statedata.State == 1">
            <div class="list_head">
                <span></span>
                <span></span>
                <span></span>
                <span>CNY</span>
                <span></span>
            </div>
            <div class="list_li" v-for="(item, index) in data" :key="index">
                <span>{{item.currency}}</span>
                <span>{{item.thawAmount.toFixed(8)}}</span>
                <span>{{item.frozenAmount.toFixed(8)}}</span>
                <span>{{item.cny.toFixed(2)}}</span>
                <span>
                    <a @click="openrechange(item.currency)"></a>
                    <a @click="openwithdraw(item.currency,item.thawAmount)"></a>
                    <Dropdown placement="bottom-end" @on-click="changeMenu" @on-visible-change="changevisible(item.currency)">
                        <a href="javascript:void(0)">
                            
                        </a>
                        <DropdownMenu slot="list">
                            <DropdownItem v-for="(item , index) in marketdic" :key="index" v-bind:name="item.TokenPair">
                              <span @click="Topage(item.Currency)">{{item.TokenPair}}</span>
                            </DropdownItem>
                        </DropdownMenu>
                    </Dropdown>
                </span>
            </div>
        </div>
        <div class="nomerchant" v-else>
            <p class="title"></p>
            <p>1CoinsUp</p>
            <p>2</p>
            <p>1 </p>
            <p>2 </p>
            <p>3 </p>
            <p>4 1-3</p>
            <p>5 1000WE201961</p>
            <Button type="primary" style='display:block;width:240px;height:53px;margin: 60px auto;font-size:18px;' @click="pageTo('merchantApply')"></Button>
        </div>
        <!--  -->
        <Modal
            v-model="rechargeModal"
            width="490">
            <p class="confirmTitle">{{rechargecurrency}}</p>
            <p class="linktitle">{{rechargecurrency}}</p>
            <p class="linklocation">{{rechargeAddress}}</p>
            <a class="copycode" @click="downloadcode"></a>
            <div class="QRcode">
              <vue-qr :text="rechargeAddress" :size="147" :margin="0"></vue-qr>
            </div>
            <ul class="rechargeDesc">
              <li>{{rechargecurrency}}</li>
              <li>1.000000{{rechargecurrency}}</li>
              <li></li>
              <li></li>
            </ul>
            <div slot="footer"></div>
        </Modal>
        <!--  -->
        <Modal
            v-model="withdrawModal"
            width="490"
            @on-ok="withdrawOk"
            >
            <p class="confirmTitle">{{withdrawcurrency}}</p>
            <Form :model="withdrawData" label-position="top">
                <FormItem>
                    <p class="title_p"><span></span><img src="../../assets/images/address.png" @click="openaddresslist"></p>
                    <Input v-model="withdrawData.toAddress" type="textarea" :autosize="{minRows: 3,maxRows: 3}" placeholder=""/>
                </FormItem>
                <FormItem label="">
                    <i-input v-model="withdrawData.amount" placeholder="1" class="amount" @on-blur="test(withdrawData.amount)"></i-input>
                    <p class="amountbtn"><span class="currency">{{withdrawcurrency}}</span><span class="allwithdraw" @click="withdrawData.amount = amount"></span></p>
                </FormItem>
                <FormItem label="">
                    <i-input v-model="withdrawData.payPassword" placeholder="" type="password"></i-input>
                </FormItem>
            </Form>
            <p class="withdraw_p"><span><i>({{this.free*100}}%)</i></span><span>{{cost}}{{withdrawcurrency}}</span></p>
            <p class="withdraw_p"><span></span><span>{{withdrawData.amount - cost}}{{withdrawcurrency}}</span></p>
        </Modal>
        <!--  -->
        <Modal
            v-model="addresslistModal"
            width="490"
            @on-ok="addresslistOk"
            @on-cancel="addresslistCancel"
            okText=""
            >
            <p class="confirmTitle">{{withdrawcurrency}}</p>
            <ul class="addresslist">
              <li v-for="(item, index) in addressList" :key="index">
                <section @click="selectaddress(item.address)">
                  <p>{{item.comment}}</p>
                  <p>{{item.address}}</p>
                </section>
                <img src="../../assets/images/cancel.png" @click="deleteaddress(item.address)">
              </li>
            </ul>
        </Modal>

    </div>
</template>

<script>
import VueQr from "vue-qr";

export default {
  data() {
    return {
      status: 1,
      apiurl: this.$common.coinspath + "/v1/user/money/coin",
      totalapi: this.$common.coinspath + "/v1/user/money/coin_total",
      marketdicapi: this.$common.coinspath + "/v1/marketdic",
      marketdataapi: this.$common.coinspath + "/v1/marketdata/",
      rechangeapi:
        this.$common.coinspath + "/v1/user/coin/query/recharge_address",
      freeapi: this.$common.coinspath + "/v1/user/coin/withdraw/fee",
      total: 0,
      data: [],
      totaldata: {
        currencyAcount: 0,
        cny: 0
      },
      marketdic: [],
      rechargeModal: false,
      withdrawcurrency: "",
      rechargecurrency: "",
      rechargeAddress: "",
      withdrawModal: false,
      withdrawData: {
        userId: this.$store.state.userstatus.userId,
        toAddress: "",
        currency: "",
        amount: 0,
        payPassword: ""
      },
      withdrawapi: this.$common.coinspath + "/v1/user/coin/withdraw",
      amount: 0,
      cost: 0,
      addresslistModal: false,
      addresslistapi: this.$common.path + "/user/getExtractAddress",
      addressList: [],
      deleteaddreddapi: this.$common.path + "/user/removeExtractAddress",
      free: 0,
      stateapi:
        this.$common.coinspath +
        "/v1/user/merchant/info/" +
        this.$store.state.userstatus.userId,
      statedata: {
        State:''
      }
    };
  },
  created() {
    this.getstate();
    this.getdatalist();
    this.gettotal();
  },
  components: { VueQr },
  methods: {
    getstate() {
      this.$axios.get(this.stateapi).then(res => {
        if (res.data.status == 1) {
          this.statedata = res.data.data;
        } else {
          this.statedata.State = 4;
        }
      });
    },
    changeMenu(val) {},
    changevisible(currency) {
      this.getmarketdic(currency);
    },
    test(val) {
      this.withdrawData.amount = this.$common.clearNoNum(val);
    },
    pageTo(path) {
      this.$router.push({ name: path });
    },
    Topage(currency){
      this.$router.push({ name: 'exchange' ,params:{currency:currency}});
    },
    getdatalist() {
      this.$axios
        .get(this.apiurl, {
          params: this.$common.sort({
            userId: this.$store.state.userstatus.userId
          })
        })
        .then(res => {
          if (res.data.status == 1) {
            this.data = res.data.data;
          } else {
            this.$Message.error(res.data.message);
          }
        });
    },
    gettotal() {
      this.$axios
        .get(this.totalapi, {
          params: this.$common.sort({
            userId: this.$store.state.userstatus.userId
          })
        })
        .then(res => {
          if (res.data.status == 1) {
            this.totaldata = res.data.data;
          } else {
            this.$Message.error(res.data.message);
          }
        });
    },
    getfree() {
      this.$axios
        .get(this.freeapi, {
          params: this.$common.sort({
            cointype: this.withdrawcurrency
          })
        })
        .then(res => {
          if (res.data.status == 1) {
            this.free = res.data.data.ServiceFee;
          } else {
            this.$Message.error(res.data.message);
          }
        });
    },
    getmarketdic(coinstype) {
      this.marketdic = [];
      this.$axios.post(this.marketdataapi + coinstype).then(res => {
        if (res.data.status == 1) {
          this.marketdic = res.data.data;
        } else {
          this.$Message.error(res.data.message);
        }
      });
    },
    openrechange(currency) {
      this.rechargeModal = true;
      this.rechargecurrency = currency;
      this.$axios
        .get(this.rechangeapi, {
          params: this.$common.sort({
            userId: this.$store.state.userstatus.userId,
            currency: currency
          })
        })
        .then(res => {
          if (res.data.status == 1) {
            this.rechargeAddress = res.data.data.Address;
          } else {
            this.$Message.error(res.data.message);
          }
        });
    },
    downloadcode() {
      var oQrcode = document.querySelector(".QRcode img");
      var url = oQrcode.src;
      var a = document.createElement("a");
      var event = new MouseEvent("click");
      a.download = "" + this.rechargecurrency + "";
      a.href = url;
      a.dispatchEvent(event);
    },
    openwithdraw(currency, amount) {
      this.withdrawModal = true;
      this.withdrawcurrency = currency;
      this.withdrawData.currency = currency;
      this.amount = amount;
      this.getfree();
    },
    withdrawOk() {
      if (this.$store.state.userstatus.isSetPayPassword == 0) {
        this.$Message.error("");
        return false;
      }
      this.$axios
        .post(this.withdrawapi, this.$common.sort(this.withdrawData))
        .then(res => {
          if (res.data.status == 1) {
            this.$Message.success(res.data.message);
          } else {
            this.$Message.error(res.data.message);
          }
        });
    },
    openaddresslist() {
      this.addresslistModal = true;
      this.withdrawModal = false;
      this.$http
        .get(this.addresslistapi, {
          params: this.$common.sort({
            userId: this.$store.state.userstatus.userId
          })
        })
        .then(res => {
          if (res.data.status == 1) {
            this.addressList = res.data.data.address;
          } else {
            this.$Message.error(res.data.message);
          }
        });
    },
    selectaddress(address) {
      this.withdrawData.toAddress = address;
      this.addresslistCancel();
    },
    addresslistOk() {
      this.addresslistModal = false;
      this.$store.state.status.addAddressModal = true;
    },
    addresslistCancel() {
      this.addresslistModal = false;
      this.withdrawModal = true;
    },
    deleteaddress(address) {
      this.$http
        .post(
          this.deleteaddreddapi,
          this.$common.sort({
            userId: this.$store.state.userstatus.userId,
            address: address
          })
        )
        .then(res => {
          if (res.data.status == 1) {
            this.openaddresslist();
            this.$Message.success(res.data.message);
          } else {
            this.$Message.error(res.data.message);
          }
        });
    }
  },
  watch: {
    withdrawData: {
      handler(curVal, oldVal) {
        this.cost = this.$common.clearNoNum(
          parseFloat(curVal.amount) * this.free
        );
      },
      deep: true
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/accountList.scss";
@import "../../assets/scss/accountmodel.scss";
.nomerchant {
  p {
    line-height: 25px;
    padding-left: 20px;
  }
  p.title {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
</style>



