import $common from '../assets/js/common';

export default {
    state: {
        nickname:$common.getCookie('nickname'), 
        token:$common.getCookie('token'),
        telephone:$common.getCookie('telephone'),
        userId:$common.getCookie('userId'),
        isPayMargin:$common.getCookie('isPayMargin'), //
        isSetPayPassword:$common.getCookie('isSetPayPassword'), //
        isRealAuth:$common.getCookie('isRealAuth'), //
        isSeniorAuth:$common.getCookie('isSeniorAuth'), //
        isSetNickname:$common.getCookie('isSetNickname'), //
        idNumber:$common.getCookie('idNumber'), //
        name:$common.getCookie('name'), //
        isSetBank:$common.getCookie('isSetBank'), //
        isSetAlipay:$common.getCookie('isSetAlipay'),//
        isSetWeixin:$common.getCookie('isSetWeixin'),//
        orderTotal:$common.getCookie('orderTotal'),//
        tradeRate:$common.getCookie('orderTotal'),//
        headIcon:$common.headIcon(),//
    }
}




